#legend {
   
    margin: 12px 0px;

    opacity: 0.9;
    z-index: 1;
    display: block;
    
  }
  #legend h4 {
    /* font: 800 12px/16px 'Montserrat'; */
    /* text-transform: uppercase; */
    color:#231F20;
    font-size: 14px;
    margin-bottom: 12px;
  }
  #legend-content{
    /* font: 800 12px/16px ; */
    font-size: 5px !important;
    margin-bottom: 6px;
  }
  .legend-content > li {
    margin-bottom: 6px;
    display: flex;
    align-items: center;
    font-size: 14px;
    color:#231F20;
    /* text-transform: uppercase; */
  }
  .legend-content > li:last-child {
    margin-bottom: 0;
  }
  .circle {
    width: 12px;
    height:12px;
    border-radius: 50%;
    margin-right: 12px;
  }